<template>
    <div class="cycle-report">
        <div class="center" v-if="loading">
            <p-progress-spinner />
        </div>

        <div v-else class="knobs-container">
            <div v-if="gData.HCGgt10_perc">
                <p-knob
                    v-model="gData.HCGgt10_perc"
                    :readonly="true"
                    :size="120"
                    :value-color="'#FFA726'"
                />
                <span class="knob-label">HCG > 10%</span>
            </div>

            <div v-if="gData.GS_perc">
                <p-knob
                    v-model="gData.GS_perc"
                    :readonly="true"
                    :size="120"
                    :value-color="'#42A5F5'"
                />
                <span class="knob-label">GS %</span>
            </div>

            <div v-if="gData.HB_perc">
                <p-knob
                    v-model="gData.HB_perc"
                    :readonly="true"
                    :size="120"
                    :value-color="'#66BB6A'"
                />
                <span class="knob-label">HB %</span>
            </div>

            <div v-if="gData.Loss_perc">
                <p-knob
                    v-model="gData.Loss_perc"
                    :readonly="true"
                    :size="120"
                    :value-color="'#EF5350'"
                />
                <span class="knob-label">Loss %</span>
            </div>
        </div>

        <div
            class="center"
            style="height: 400px; width: 600px; margin-left: 100px; margin-top: 40px;"
        >
            <!-- Adjust height and width here -->
            <Bar
                :chart-data="chartData"
                :options="chartOptions"
                :height="400"
                :width="600"
            />
        </div>
    </div>
</template>

<script>
import { useCycleReportTable } from "@/composable/cycle-report";
import { computed, onMounted, onUnmounted } from "vue";
import Knob from "primevue/knob";
import { emitter } from "@/event-bus";

import { Bar } from "vue-chartjs";

import {
    Chart as ChartJS,
    Title,
    Tooltip,
    Legend,
    BarElement,
    CategoryScale,
    LinearScale
} from "chart.js";

ChartJS.register(
    Title,
    Tooltip,
    Legend,
    BarElement,
    CategoryScale,
    LinearScale
);

export default {
    components: {
        "p-knob": Knob,
        Bar
    },

    setup() {
        const { store, getCycleKPIC, loading } = useCycleReportTable();
        const gData = computed(() => store.state.cycleReport.gData);

        const chartData = computed(() => ({
            labels: ["HCG > 10%", "GS%", "HB%", "Loss%"],
            datasets: [
                {
                    label: "KPI-C",
                    data: [
                        gData.value.HCGgt10_perc,
                        gData.value.GS_perc,
                        gData.value.HB_perc,
                        gData.value.Loss_perc
                    ],
                    backgroundColor: [
                        "#FFA726",
                        "#42A5F5",
                        "#66BB6A",
                        "#EF5350"
                    ],
                    borderColor: ["#FB8C00", "#1E88E5", "#43A047", "#E53935"],
                    borderWidth: 1
                }
            ]
        }));

        const chartOptions = {
            responsive: true,
            scales: {
                y: {
                    beginAtZero: true,
                    max: 100
                }
            }
        };

        onMounted(() => {
            getCycleKPIC();

            emitter.on("cycle-report-table:get-cycle-kpic", getCycleKPIC);
        });

        onUnmounted(() => {
            emitter.off("cycle-report-table:get-cycle-kpic", getCycleKPIC);
        });

        return {
            gData,
            chartData,
            loading,
            chartOptions
        };
    }
};
</script>

<style scoped>
.cycle-report {
    width: 100%; /* Use a percentage for responsive design */
    max-width: 800px; /* Set a maximum width */
}

.chart-container {
    height: 400px; /* Adjust the height here */
}

.knobs-container {
    display: flex;
    justify-content: space-around;
    margin-bottom: 20px;
}

.center {
    display: flex;
    align-items: center;
    justify-content: center;
}
</style>
